.home {
  position: relative;
  width: 100%;
  height: 100%;
}

.home .alert-wrapper {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 101;
}

.home .home-list {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 80%;
  height: 100%;
  background: #fff;
  transform: translate(-100%);
  transition: all 0.3s ease-in-out;
}

.home .home-list .home-list-header {
  height: 1.88rem;
  background: #f9c23b;
  margin-bottom: 0.16rem;
}

.home .home-list .home-list-content ul li .list-item {
  display: -ms-flexbox;
  display: flex;
  height: 0.96rem;
  line-height: 0.98rem;
  color: #000;
}

.home .home-list .home-list-content ul li .list-item.active {
  background: #fbd579;
  color: #fff;
}

.home .home-list .home-list-content ul li .list-item .list-theme {
  position: relative;
  width: 1.16rem;
}

.home .home-list .home-list-content ul li .list-item .list-theme .color-block-wrapper {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.home .home-list .home-list-content ul li .list-item .list-name {
  -ms-flex: 1;
  flex: 1;
  font-size: 0.3rem;
  text-indent: 0.36rem;
}

.home .home-list .home-list-content ul li .list-item .list-count {
  width: 1.04rem;
  font-size: 0.21rem;
  color: #5d502e;
  text-align: center;
}

.home .home-list .home-list-content .add-list {
  display: -ms-flexbox;
  display: flex;
  height: 0.98rem;
  line-height: 0.98rem;
}

.home .home-list .home-list-content .add-list .add-list-icon {
  width: 1.16rem;
  height: 100%;
  font-size: 0.3rem;
  text-align: center;
  color: #6f6f6f;
}

.home .home-list .home-list-content .add-list .add-list-content {
  -ms-flex: 1;
  flex: 1;
  text-indent: 0.36rem;
  font-size: 0.3rem;
  font-weight: bold;
}

.home .home-content {
  position: absolute;
  left: 0;
  right: 0;
  top: 1.18rem;
  bottom: 0;
}

.home .home-content .categoire-wrapper {
  display: inline-block;
  width: 50%;
  height: 50%;
}

.home .home-content .add-button-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.home .home-content .theme-wrapper {
  position: absolute;
  bottom: 0;
  width: 100%;
  z-index: 300;
}


