.color-block {
  width: 1.46rem;
  height: 1.46rem;
}

.color-block.small {
  width: 0.46rem;
  height: 0.46rem;
}

.color-block div {
  float: left;
  width: 47%;
  height: 47%;
  background: #fff;
  font-size: 0;
}

.color-block div:nth-child(odd) {
  margin-right: 6%;
}

.color-block div:nth-child(1) {
  margin-bottom: 6%;
}

.color-block div:nth-child(2) {
  margin-bottom: 6%;
}


