.alert {
  box-sizing: border-box;
  width: 86%;
  padding: 0.5rem;
  border: 0.01rem solid #e0e6e6;
  border-radius: 0.06rem;
  background: #fff;
  margin: 0 auto;
}

.alert .alert-header {
  height: 0.94rem;
  font-size: 0.32rem;
  vertical-align: top;
  font-weight: bold;
}

.alert .alert-content {
  height: auto;
  font-size: 0;
}

.alert .alert-content input {
  width: 100%;
  height: 0.5rem;
  margin-bottom: 0.94rem;
  border-bottom: 0.04rem solid #c7c7c7;
  outline: none;
  font-size: 0.28rem;
  line-height: 0.58rem;
}

.alert .alert-footer {
  width: 100%;
  height: 0.22rem;
  line-height: 0.22rem;
  font-size: 0;
}

.alert .alert-footer .btn-wrapper {
  float: right;
}

.alert .alert-footer .btn-wrapper button {
  font-size: 0.22rem;
  color: #008d7e;
  font-weight: bold;
}

.alert .alert-footer .btn-wrapper button:first-child {
  margin-right: 0.64rem;
}

.alert .alert-footer .btn-wrapper button:last-child {
  margin-right: 0.16rem;
}


