.categorieBlock {
  font-size: 0;
  line-height: 1;
}

.categorieBlock.big {
  width: 0.58rem;
  height: 0.58rem;
}

.categorieBlock.small {
  width: 0.36rem;
  height: 0.36rem;
}

.categorieBlock div {
  float: left;
  box-sizing: border-box;
  width: 46%;
  height: 46%;
  border: 0.01rem solid #000;
}

.categorieBlock div:nth-child(even) {
  margin-left: 8%;
}

.categorieBlock div:nth-child(1),
.categorieBlock div:nth-child(2) {
  margin-bottom: 8%;
}


