.circle {
  box-sizing: border-box;
  width: 2.9rem;
  height: 2.9rem;
  font-size: 0.7rem;
}

.circle .inner {
  box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 0.38rem solid #cac2b4;
  text-align: center;
}

.circle .inner p {
  -ms-flex: 1;
  flex: 1;
  font-size: 100%;
  line-height: 100%;
  color: #595b5b;
  font-weight: bold;
  padding-bottom: 10%;
}

.circle .inner span {
  position: absolute;
  display: inline-block;
  width: 100%;
  left: 0;
  bottom: 16%;
  font-size: 40%;
  color: #86868a;
}


